  height: 3px;
  background-color: #F5F5F5;
}

#divdragdropbody::-webkit-scrollbar-thumb {
  background-clip: padding-box;
}

.formtable table .ant-table-thead tr th {
  background-color: white !important;
  border-bottom-color: #C7C7C7;
  border-bottom-width: 2px;
  height: 5vh !important;
  max-height: 5vh !important;
  line-height: 0px;
  font-weight: 600;
}

.formtable table tbody tr td {
  border-bottom-color: #C7C7C7;
  line-height: 15px;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
}

.formtable li.ant-pagination-item {
  border: 0px solid !important;
}

.formtable li.ant-pagination-item a {
  color: #595959;
}

.formtable li.ant-pagination-item-active a {
  color: white !important;
  background-color: #00A793 !important;
  border: 0px solid !important;
  border-radius: 3px !important;
}

.formtable li button.ant-pagination-item-link {
  position: relative;
  top: -4px;
}

.formtable .ant-table-pagination.ant-pagination {
  margin-bottom: 0px !important;
}

.menudrawer .ant-drawer-title {
  color: #8A939C;
}

.menudrawer .ant-drawer-close {
  color: #8A939C;
}

.formpopup .ant-drawer-header {
  display: none;
}

.formpopup .ant-drawer-body {
  padding: 0;
}

.antimground img {
  border: 1px solid #CACACA;
  border-radius: 15px;
}

/* Let's get this party started */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #E9ECEF;
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
::-webkit-scrollbar-thumb:window-inactive {
background: #E9ECEF
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#nav-icon1 {
    width: 35px;
    height: 35px;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    z-index: 100;
    background-color: transparent;
    opacity: 0.8;
    margin-left: 5px;
  }
  
  #nav-icon1 span {
    position: absolute;
    height: 5px;
    width: 90%;
    background: white;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }
  
  #nav-icon1 span:nth-child(1) {
    top: 5px;
    left: 1px;
  }
  
  #nav-icon1 span:nth-child(2) {
    top: 15px;
    left: 1px;
  }
  
  #nav-icon1 span:nth-child(3) {
    top: 25px;
    left: 1px;
  }
  
  #nav-icon1.open span:nth-child(1) {
    top: 15px;
    transform: rotate(135deg);
  }
  
  #nav-icon1.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
  #nav-icon1.open span:nth-child(3) {
    top: 15px;
    transform: rotate(-135deg);
  }
